<template>
  <div class="entryData">
    <d2-container>
      <template slot="header">
        <div class="flex ryt-right-top">
          <div class="line_header"></div>整体数据
        </div>
        <!-- 数据筛选 -->
        <div  class="menuPartClass">
          <el-form
            :inline="true"
            :model="form"
            size="mini"
            label-width="80px"
          >
            <el-row class="dataScreen">
              <el-button
                class="el-icon-refresh"
                size="mini"
                @click="refreshPageClick"
                style="float: right; margin-top: 10px;margin-right:10px"
                >刷新</el-button>


              <el-form-item
                label-width="80px"
                style="margin-top :10px;margin-bottom:0px;margin-left:11px"
                prop="media_platform_id_arr"
              >
                <el-select
                  style="width:180px; margin-right:8px;"
                  v-model="form.media_platform_id_arr"
                  placeholder="请选择平台"
                  filterable
                  size="mini"
                  multiple
                  collapse-tags
                  @change="filterMedia($event)"
                >
                  <el-option
                    v-for="(item,index) in mediaPlatformArr"
                    :key="index"
                    :label="item.label"
                    :value="item.value"
                    :id="item.id"
                  ></el-option>
                </el-select>
              </el-form-item>

              <el-form-item
                label-width="80px"
                style="margin-top: 10px;margin-bottom:0px"
                prop="media_id_arr"
              >
                <el-select
                  style="width:180px; "
                  v-model="form.media_id_arr"
                  placeholder="请选择应用"
                  filterable
                  size="mini"
                  multiple
                  collapse-tags
                  @change="filterPosition($event)"
                >
                  <el-option
                    v-for="(item, index) in mediaArr"
                    :key="index"
                    :label="item.label"
                    :value="item.value"
                    :id="item.id"
                  ></el-option>
                </el-select>
              </el-form-item>

              <el-form-item
                label-width="80px"
                style="margin-top: 10px;margin-bottom:0px"
                prop="position_id_arr"
              >
                <el-select
                  v-model="form.position_id_arr"
                  placeholder="请选择广告位"
                  style="width:180px; "
                  filterable
                  size="mini"
                  multiple
                  collapse-tags
                  @change="filterpos()"
                >
                  <el-option
                    v-for="(item,index) in positionArr"
                    :key="index"
                    :label="item.label"
                    :value="item.value"
                  ></el-option>
                </el-select>
              </el-form-item>

              <el-form-item
                label-width="80px"
                style="margin-top: 10px;margin-bottom:0px"
              >
                <el-date-picker
                  :clearable="false"
                  v-model="timeValue"
                  style="width: 210px;"
                  type="daterange"
                  size="mini"
                  unlink-panels
                  range-separator="至"
                  start-placeholder="开始日期"
                  end-placeholder="结束日期"
                  format="yyyy-MM-dd"
                  value-format="yyyy-MM-dd"
                  @change="dateChange"
                  :picker-options="pickerOptions"
                ></el-date-picker>
                  </el-form-item>



                  <div class="listMenuPartClass">
                    <!-- <el-button 
                        size="mini" 
                        type="primary"
                        slot="reference"
                        style="margin-left:10px;"
                        @click="inputShareButton()"
                        >
                      <d2-icon name="download" /> 纷享报表
                    </el-button> -->
                <el-button  size="mini" style="margin-right:10px;" type="primary" @click="exportBtn">
                  <d2-icon name="plus" />导出
                </el-button>
                <el-popover :offset="500" placement="bottom-end" width="350" v-model="dateVisible">
                  <div>
                    <el-date-picker
                      :clearable="false"
                      v-model="timeValueRelease"
                      style="width: 300px; margin-right: 20px"
                      type="daterange"
                      unlink-panels
                      range-separator="至"
                      start-placeholder="开始日期"
                      end-placeholder="结束日期"
                      format="yyyy-MM-dd"
                      value-format="yyyy-MM-dd"
                      @change="dateChangeRelease"
                      :picker-options="pickerOptionsO"
                    ></el-date-picker>
                  </div>
                  <el-button  size="mini" type="primary" slot="reference" >
                  <i class="fa fa-eye" aria-hidden="true"></i>  概览发布
                  </el-button>
                </el-popover>
              </div>


            
            </el-row>
          </el-form>
        </div>
      </template>
        <!-- ************表格展示*********************** -->
       
        <!-- **********表格开始**********8 -->
        <el-table
          border
          ref="multipleSelection"
          :data="listDataArray"
          size="small"
          style="margin-top:-20px"
          :header-cell-style="{ background: '#f5f5f5' }"
          :row-class-name="tableRowClassName"
        >
          <el-table-column
            label="日期"
            align="center"
            min-width="120"
            prop="date"
            key="1"
            fixed="left"
          >
            <template slot-scope="scope">{{scope.row.date ? scope.row.date : "-"}}</template>
          </el-table-column>
          <el-table-column
            label="平台名称"
            align="center"
            min-width="120"
            prop="media_platform_name"
            fixed="left"
          >
            <template slot-scope="scope">{{scope.row.media_platform_name ? scope.row.media_platform_name : "-"}}</template>
          </el-table-column>
          <el-table-column
            label="应用名称"
            align="center"
            min-width="120"
            prop="media_name"
            fixed="left"
          >
            <template slot-scope="scope">{{scope.row.media_name ? scope.row.media_name : "-"}}</template>
          </el-table-column>
          <el-table-column
            label="广告位名称"
            align="center"
            min-width="120"
            prop="position_name"
            fixed="left"
          >
            <template slot-scope="scope">{{scope.row.position_name ? scope.row.position_name : "-"}}</template>
          </el-table-column>
          <el-table-column
            label="结算方式"
            align="center"
            min-width="120"
            prop="bill_type"
          >
            <template slot-scope="scope">
              <div v-if="scope.row.bill_type == 1">CPM</div>
              <div v-if="scope.row.bill_type == 2">CPC</div>
              <div v-if="scope.row.bill_type == 3">CPA</div>
              <div v-if="scope.row.bill_type == 4">CPS</div>
              <div v-if="scope.row.bill_type == 5">CPD</div>
            </template>
          </el-table-column>
          <el-table-column
            label="CPA单价"
            align="center"
            min-width="120"
            prop="cpa_reserve_price"
          >
            <template slot-scope="scope">{{scope.row.cpa_reserve_price ? scope.row.cpa_reserve_price : "-"}}</template>
          </el-table-column>
          <el-table-column
            label="CPS比例"
            align="center"
            min-width="120"
            prop="cps_reserve_price"
          >
            <template slot-scope="scope">{{scope.row.cps_reserve_price ? scope.row.cps_reserve_price : "-"}}</template>
          </el-table-column>
          <el-table-column
            label="统计转化数"
            align="center"
            min-width="120"
            prop="sum_right_dp_pv"
          >
            <template slot-scope="scope">{{scope.row.sum_right_dp_pv}}</template>
          </el-table-column>
          <el-table-column
            label="录入转化数"
            align="center"
            min-width="120"
            prop="input_right_dp_pv"
          >
            <template slot-scope="scope">{{scope.row.input_right_dp_pv}}</template>
          </el-table-column>
          <el-table-column
            label="结算转化数"
            align="center"
            min-width="120"
            prop="bill_right_dp_pv"
          >
            <template slot-scope="scope">{{scope.row.bill_right_dp_pv}}</template>
          </el-table-column>
          <el-table-column
            label="统计支出"
            align="center"
            min-width="120"
            prop="cost"
          >
            <template slot-scope="scope">{{scope.row.cost}}</template>
          </el-table-column>
          <el-table-column
            label="录入支出"
            align="center"
            min-width="120"
            prop="input_spending"
          >
            <template slot-scope="scope">{{scope.row.input_spending}}</template>
          </el-table-column>
          <el-table-column
            label="结算支出"
            align="center"
            min-width="120"
            prop="bill_amount"
          >
            <template slot-scope="scope">{{scope.row.bill_amount}}</template>
          </el-table-column>
          <el-table-column
            label="发布状态"
            align="center"
            min-width="120"
            prop="status"
            fixed="right"
          >
            <template slot-scope="scope">
              <div v-if="scope.row.status == 0">未发布</div>
              <div v-if="scope.row.status == 1">已发布</div>
            </template>
          </el-table-column>
        </el-table>
      <el-pagination
        slot="footer"
        :current-page="listQuery.current"
        :page-size="listQuery.pageSize"
        :total="total"
        :page-sizes="[10,20,40,60]"
        layout="total, sizes, prev, pager, next, jumper"
        style="margin:-10px;"
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
      ></el-pagination>
    </d2-container>
  </div>
</template>

<script>
import util from "@/libs/util";
export default {
  name:"entryData",
  data(){
    return{
      form:{
        media_platform_id_arr:[],
        media_id_arr:[],
        position_id_arr:[],
      },
      dateVisible:false,
      mediaPlatformArr:[],//流量平台数组
      mediaArr:[],//媒体数组
      positionArr:[],//广告位数组
      platformFilterArr:[],//流量平台id筛选数组
      mediaFilterArr:[],//媒体平台id筛选数组
      timeValue:[],//时间
      timeValueRelease:[],//发布时间
      listQuery: {
        pageSize: 10,
        current: 1,
        sort: "",
        order: "",
      },
      total:0,
      listDataArray:[],//列表数据
      pickerOptionsO:{
        disabledDate(time) {
          //禁止选择当前日期和以后日期
          return time.getTime() > Date.now() - 8.64e7;
        },
      },
      pickerOptions: {
        disabledDate(time) {
          //禁止选择当前日期和以后日期
          return time.getTime() > Date.now() - 8.64e7;
        },
        shortcuts: [
          {
            text: "今日",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              picker.$emit("pick", [start, end]);
            },
          },
          {
            text: "昨日",
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() - 3600 * 1000 * 24);
              picker.$emit("pick", [date, date]);
            },
          },
          {
            text: "本周",
            onClick(picker) {
              const start = new Date();
              const end = new Date();
              const nows = start.getDay() || 7;
              start.setTime(start.getTime() - 3600 * 1000 * 24 * (nows - 1));
              picker.$emit("pick", [start, end]);
            },
          },
          {
            text: "上周",
            onClick(picker) {
              const dataValue = new Date();
              const year = dataValue.getFullYear();
              const month = dataValue.getMonth() + 1;
              const day = dataValue.getDate();
              var thisWeekStart; //本周周一的时间
              if (dataValue.getDay() == 0) {
                //周天的情况；
                thisWeekStart =
                  new Date(year + "/" + month + "/" + day).getTime() -
                  (dataValue.getDay() + 6) * 86400000;
              } else {
                thisWeekStart =
                  new Date(year + "/" + month + "/" + day).getTime() -
                  (dataValue.getDay() - 1) * 86400000;
              }
              const prevWeekStart = thisWeekStart - 7 * 86400000; //上周周一的时间
              const prevWeekEnd = thisWeekStart - 1 * 86400000; //上周周日的时间
              const start = new Date(prevWeekStart); //开始时间
              const end = new Date(prevWeekEnd); //结束时间
              picker.$emit("pick", [start, end]);
            },
          },
          {
            text: "本月",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setDate(1);
              picker.$emit("pick", [start, end]);
            },
          },
          {
            text: "上月",
            onClick(picker) {
              const start = new Date();
              const end = new Date(start);
              end.setMonth(start.getMonth());
              start.setMonth(start.getMonth() - 1);
              end.setDate(0);
              start.setDate(1);
              picker.$emit("pick", [start, end]);
            },
          },
          {
            text: "最近七天",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 6);
              picker.$emit("pick", [start, end]);
            },
          },
          {
            text: "最近30天",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit("pick", [start, end]);
            },
          },
        ],
      },
    }
  },
  created(){
    this.getMediaPlatform();
    this.getMedia();
    this.getPosition();
    this.getSevenDayDate();
    this.fixedForm();
  },
  methods:{
    inputShareButton(){
      //导出纷享报表
      let url = this.baseUrl.ip1+`/enterData/excel/fxk`
      var params={
            start_date: this.timeValue[0],
            end_date:this.timeValue[1],
      }
      var newpa = this.changeParam(params);
      let token =''
      token=util.cookies.get('token_wharf')
      window.location.href = url + "?token=" + token +newpa
    },
    changeParam(params){
      return JSON.stringify(params)
          .replace(/:/g, "=")
          .replace(/,/g, "&")
          .replace(/{/g, "&")
          .replace(/}/g, '')
          .replace(/"/g,'' )
    },
    dateChange(val) {
        //天
      this.timeValue = val;
      this.listQuery.current = 1
      this.fixedForm();
    },
    getSevenDayDate() {
      //时间 默认最近7天安排上
      const end = new Date();
      const start = new Date();
      start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
      //时间格式转换
      const endResDate =
        end.getFullYear() + "-" + (end.getMonth() + 1) + "-" + end.getDate();
      const startResDate =
        start.getFullYear() +
        "-" +
        (start.getMonth() + 1) +
        "-" +
        start.getDate();
      this.timeValue = [startResDate, endResDate];
    },
    async fixedForm(){
      //表格数据内容
      // const {data:res} = await this.$http.post('10.10.10.8:38080/app/mock/data/161',
      const {data:res} = await this.$http.post(this.baseUrl.ip1+`/cost_publish_data/data_list`,
        {
          media_platform_id_arr:this.form.media_platform_id_arr,
          media_id_arr:this.form.media_id_arr,
          position_id_arr:this.form.position_id_arr,
          start_date: this.timeValue[0],
          end_date: this.timeValue[1],
          limit: this.listQuery.pageSize,
          page: this.listQuery.current,
        }
      );
      this.listDataArray = res.data;
      this.listQuery.current = res.current_page;
      this.listQuery.limit = res.per_page;
      this.total = res.total;
      console.log(res.total)
      this.gettotalData();
    },
    async gettotalData(){
      //合计
      var params = {
        media_platform_id_arr:this.form.media_platform_id_arr,
        media_id_arr:this.form.media_id_arr,
        position_id_arr:this.form.position_id_arr,
        start_date: this.timeValue[0],
        end_date: this.timeValue[1],
        limit: this.listQuery.pageSize,
        page: this.listQuery.current,
      }
      const response = await this.$http.post(this.baseUrl.ip1 + `/cost_publish_data/data_total`,params)
      .then(response=>{
        if(response.code == 200){
          this.listDataArray.unshift(response.data);
        }
      })
    },
    async exportBtn(){
      //导出
      const {data:res} = await this.$http.post(
        this.baseUrl.ip1+`/cost_publish_data/data_export`,
        {
          media_platform_id_arr:this.form.media_platform_id_arr,
          media_id_arr:this.form.media_id_arr,
          position_id_arr:this.form.position_id_arr,
          start_date: this.timeValue[0],
          end_date: this.timeValue[1],
          limit: this.listQuery.pageSize,
          page: this.listQuery.current,
        }
      );
      window.location.href = res.url;
    },
    dateChangeRelease(){
      // //点击发布跳转子页面
      this.$router.push({
        path:`overallData/releaseData`,
        query:{
          start_date:this.timeValueRelease[0],
          end_date:this.timeValueRelease[1],
        }
      })

    },
    handleSizeChange(val){
      //分页
      this.listQuery.pageSize = val;
      this.listQuery.current = 1;
      this.fixedForm();
    },
    async handleCurrentChange(val){
      //跳转
      console.log('aasa',this.listQuery.pageSize)
      this.listQuery.current = val;
      this.loading = true;
      const { data: res } = await this.$http.post(
        this.baseUrl.ip1 + `/cost_publish_data/data_list`,
        {
          media_platform_id_arr:this.form.media_platform_id_arr,
          media_id_arr:this.form.media_id_arr,
          position_id_arr:this.form.position_id_arr,
          start_date: this.timeValue[0],
          end_date: this.timeValue[1],
          limit: this.listQuery.pageSize,
          page: this.listQuery.current,
        }
      );
      this.listDataArray = res.data;
      this.listQuery.current = res.current_page;
      this.listQuery.limit = res.per_page;
      this.total = res.total;
      this.gettotalData();
    },
    tableRowClassName({row,rowIndex}){
      if (rowIndex === 0) {
        return "success-row";
      }
      return "";
    },
    refreshPageClick(){
      //刷新
      this.fixedForm();
    },
    async getMediaPlatform(){
      //流量平台下拉数组
      const{ data:res} = await this.$http.get(this.baseUrl.ip1 +`/sourceSys/options/mediaPlatform`)
      const lista = res;
      lista.forEach((item)=>{
        this.mediaPlatformArr.push({
          label:item.media_platform_name,
          value:item.id,
          id:item.id
        })
      })
    },
    filterMedia(platFilterArr){
      //点击流量平台筛选媒体
      this.form.media_id_arr = [];
      this.form.position_id_arr = [];
      this.platformFilterArr.splice(0,this.platformFilterArr.length)
      platFilterArr.forEach((item)=>{
        this.mediaPlatformArr.forEach((items)=>{
          if(item==items.value){
            this.platformFilterArr.push(items.id)
          }
        })
      })
      this.getMedia(this.platformFilterArr)
      this.getPosition();
      this.listQuery.current = 1
      this.fixedForm();
    },
    async getMedia(arr){
      //媒体下拉数组
      this.form.position_id_arr = [];
      this.mediaArr.splice(0,this.mediaArr.length)
      const { data: res } = await this.$http.post(
        this.baseUrl.ip1 + `/sourceSys/options/media`,
        {
          media_platform_id_arr:arr
        }
      );
      const lista = res;
      lista.forEach((item, index) => {
        this.mediaArr.push({
          label: item.media_name,
          value: item.id,
          id: item.id
        });
      });
    }, 
    filterPosition(medFilterArr){
      //点击媒体筛选广告位
      this.form.position_id_arr = [];
      this.mediaFilterArr.splice(0,this.mediaFilterArr.length)
      medFilterArr.forEach((item)=>{
        this.mediaArr.forEach((items)=>{
          if(item==items.value){
            this.mediaFilterArr.push(items.id)
          }
        })
      })
      //var mediaStr = this.mediaFilterArr.join(',')
      this.getPosition(this.mediaFilterArr);
      this.listQuery.current = 1
      this.fixedForm();
      // this.getChartData();
    },
    async getPosition(arr){
      this.positionArr.splice(0,this.positionArr.length)
      //广告位下拉
      const { data: res } = await this.$http.post(
        this.baseUrl.ip1 + `/sourceSys/options/getSelectPositionList`,
        {
          media_id_arr: arr
        }
      );
      const lista = res;
      lista.forEach((item, index) => {
        this.positionArr.push({
          value: item.id,
          label: item.media_position_name,
        });
      });
    },
    filterpos(){
      this.listQuery.current = 1
      this.fixedForm();
    },
  }
}
</script>

<style lang="scss">
.el-table .success-row {
  background: #f0f9eb;
}
.entryData {
  background-color: #f0f9eb;

  .menuPartClass{
      margin-top: 20px;
      width:100%;
      min-height: 88px; 
      background-color: white;
      box-shadow: 0 2px 8px 0 #dff3ff;
      padding-bottom: 5px;
      border-radius: 8px;

      .listMenuPartClass{
        float:right;
        margin-top: 55px;
        margin-right: -70px;
      }
    }
    .el-table__header tr,
      .el-table__header th {
        padding: 0;
        height: 40px;
    }
    .el-table__body tr,
      .el-table__body td {
        padding: 0;
        height: 35px;
    }

  
  
}
</style>
<vue-filename-injector>
export default function (Component) {
  Component.options.__source = "src/views/pages/dataFormReport/overallData/index.vue"
}
</vue-filename-injector>
